function globalBorder() {
    let globalBorder = document.querySelector('.global-border');

    function heightSize() {
        if (window.innerWidth > document.querySelector('body').clientWidth) {
            let bodyHeight = document.querySelector('body').offsetHeight;
            globalBorder.style.cssText = `
            height: ${bodyHeight * 0.94}px;
            transform: translate(-50%, 0);
            top: ${bodyHeight * 0.94 * 0.03}px;
            `;
        } else {
            globalBorder.style.cssText = `
            height: 94%;
            width: 96%;
            transform: translate(-50%, -50%);
            left: 50%;
            top: 50%;
            `;
        }
    }

    heightSize();
       
    window.addEventListener('resize', () => {
        heightSize();
    });
}
export default globalBorder;